<template>
  <div id="single-blog">
    <nav-bar></nav-bar>
    <h1>{{blog.title}}</h1>
    <article>{{blog.content}}</article>
    <p>作者：{{blog.author}}</p>
    <p>分类：</p>
    <ul>
      <li v-for="(category, index) in blog.categories" :key="index">
        {{category}}
      </li>
    </ul>
  </div>
</template>

<script>
import NavBar from './navbar/NavBar'

import { request } from "../network/request";

export default {
  name: "SingleBlog",
  data() {
    return {
      id: this.$route.params.id,
      blog: {
      }
    }
  },
  components: {
    NavBar
  }, 
  created() {
    request({
      url: '/posts' + '/' + this.id + '.json',
      method: 'get'
    }).then(item => {
      console.log(item)
      this.blog = item.data
    })
  }
}
</script>

<style scoped>
  #single-blog {
    margin: 72px auto;
    max-width: 680px;
    padding: 0 20px 20px 20px;
    background-color: #eee;
    border: 1px dotted #aaa;
  }
</style>


